ntrolsContainer .mplayer_tools {\n  display: flex;\n  gap: .5rem;\n  padding: .25rem;\n  align-items: center;\n}\n\n.mplayer_controlsContainer .mplayer_tools button {\n  background: none;\n  border: none;\n  color: inherit;\n  padding: 0;\n  height: 30px;\n  width: 30px;\n  font-size: 1.1rem;\n  cursor: pointer;\n  opacity: .85;\n  transition: opacity 150ms ease-in-out;\n}\n\n.mplayer_controlsContainer .mplayer_tools button:hover {\n  opacity: 1;\n}\n\n.mplayer_volume {\n  display: flex;\n  align-items: center;\n}\n\n.mplayer_volumeSlider {\n  width: 0;\n  transform-origin: left;\n  transform: scale(0);\n  transition: width 150ms ease-in-out, transform 150ms ease-in-out, opacity 150ms ease-in-out;\n  opacity: 0;\n  color: white;\n  background-color: white;\n  -webkit-appearance: none;\n  height: 3px;\n  border-radius: 3px;\n}\n\n.mplayer_volumeSlider::-webkit-slider-thumb {\n  -webkit-appearance: none;\n  appearance: none;\n  width: 10px;\n  height: 10px;\n  border-radius: 50%;\n  cursor: pointer;\n  background-color: white;\n  color: white;\n}\n\n.mplayer_volume:hover .mplayer_volumeSlider,\n.mplayer_volumeSlider:focus-within {\n  width: 80px;\n  transform: scale(1);\n  opacity: 1;\n}\n\n.mplayer_duration {\n  display: flex;\n  align-items: center;\n  gap: .25rem;\n  flex-grow: 1;\n  font-weight: 100;\n  font-size: .75rem;\n}\n\n.mplayer_volumeUpIcon,\n.mplayer_volumeDownIcon,\n.mplayer_mutedIcon {\n  display: none;\n}\n\n.mplayer_sizer.theater,\n.mplayer_sizer.fullscreen {\n  max-width: initial;\n  width: 100%;\n}\n\n.mplayer_sizer.theater {\n  max-height: 90vh;\n}\n\n.mplayer_sizer.fullscreen {\n  max-height: 100vh;\n}\n\n.mplayer_timelineContainer {\n  height: 6px;\n  margin-inline: .5rem;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n}\n\n.mplayer_timeline {\n  background-color: rgba(100, 100, 100, .5);\n  height: 3px;\n  width: 100%;\n  position: relative;\n  transition: height 100ms ease-in-out;\n}\n\n.mplayer_sizer.scrubbing .mplayer_previewImage,\n.mplayer_timelineContainer:hover .mplayer_previewImage {\n  display: block;\n}\n\n.mplayer_sizer:not(.images) .mplayer_previewImage {\n  display: none !important;\n}\n\n.mplayer_timelineContainer:hover .mplayer_timeline {\n  height: 100%;\n}\n\n.mplayer_thumbIndicator {\n  --scale: 0;\n  position: absolute;\n  transform: translateX(-50%) scale(var(--scale));\n  height: 200%;\n  top: -50%;\n  left: calc(var(--mplayer-played-position) * 100%);\n  background-color: $themeColor;\n  box-shadow: 0 0 16px $themeColor;\n  border-radius: 50%;\n  transition: transform 150ms ease-in-out;\n  aspect-ratio: 1 / 1;\n}\n\n.mplayer_timelineContainer:hover .mplayer_thumbIndicator {\n  --scale: 1;\n}\n\n/* preview timeline */\n.mplayer_previewTimeline {\n  content: \"\";\n  position: absolute;\n  left: 0;\n  top: 0;\n  bottom: 0;\n  /*background-color: rgba(190, 190, 190, 0.9);*/\n  right: calc(100% - var(--mplayer-preview-position, .0) * 100%);\n  /*display: none;*/\n}\n\n/* buffered timeline */\n.mplayer_timeline::before {\n  content: \"\";\n  position: absolute;\n  left: 0;\n  top: 0;\n  bottom: 0;\n  background-color: rgba(150, 150, 159, .9);\n  right: calc(100% - var(--mplayer-buffered-position) * 100%);\n  /*display: none;*/\n}\n\n/* played timeline */\n.mplayer_timeline::after {\n  content: \"\";\n  position: absolute;\n  left: 0;\n  top: 0;\n  bottom: 0;\n  background-color: $lightThemeColor;\n  right: calc(100% - var(--mplayer-played-position) * 100%);\n  /*display: none;*/\n}\n\n.mplayer_previewImage {\n  position: absolute;\n  left: calc(var(--mplayer-preview-image-position) * 100%);\n  bottom: 30px;\n  width: 200px;\n  height: 115px;\n  border: 2px solid rgba(0, 0, 0, 0);\n  transform: translate(-50%);\n  border-radius: 5px;\n  box-shadow: 0 0 0 3px $themeColor;\n  backdrop-filter: blur(.5vw);\n  display: none;\n}\n\n.mplayer_screenshotSuit {\n  display: none;\n}",".mplayer_dialog {\n  position: absolute;\n  top: .5rem;\n  left: .5rem;\n  background-color: rgba(0, 0, 0, 0.8);\n  border-radius: 5px;\n  width: 50%;\n  max-height: 80%;\n  overflow: scroll;\n  color: white;\n  display: flex;\n  flex-direction: column;\n  padding: 16px;\n  z-index: 2;\n  backdrop-filter: blur(var(--blur));\n}\n\n.mplayer_dialogTitle {\n  height: 10%;\n  font-weight: 800;\n  text-align: center;\n  transition: background-color 150ms ease-in-out;\n}\n\n.mplayer_detailsList {\n  flex-grow: 1;\n}\n\n.mplayer_dialogActions {\n  display: flex;\n  justify-content: flex-end;\n  gap: .25rem;\n}\n\n.mplayer_detailsItem {\n  display: flex;\n  white-space: nowrap;\n  overflow: auto;\n  margin-inline: .5rem;\n}\n\n.mplayer_detailsItemTitle, .mplayer_detailsItemDate {\n  display: inline-block;\n}\n\n.mplayer_dialogButton {\n  outline: none;\n  background: none;\n  font-weight: 800;\n  border: none;\n  cursor: pointer;\n  font-size: 1rem;\n  opacity: .85;\n  transition: opacity 150ms ease-in-out;\n}\n\n.mplayer_dialogButton:hover {\n  opacity: 1;\n}",".mplayer_settings, .mplayer_playlist, .mplayer_subtitles {\n  position: relative;\n}\n\n.mplayer_settingsPanel {\n  position: absolute;\n  right: -50%;\n  bottom: 50px;\n  white-space: nowrap;\n  border-radius: 2px;\n  background: rgba(28, 28, 28, 0.5);\n  padding: 7px 0;\n  z-index: 2;\n  backdrop-filter: blur(var(--blur));\n  opacity: 0;\n  visibility: hidden;\n  transition: opacity 150ms ease-in-out;\n  max-height: 200px;\n  overflow: auto;\n}\n\n.mplayer_settingsItem {\n  text-align: center;\n  cursor: pointer;\n  padding: .15rem 1.5rem;\n  margin: .1rem .1rem;\n}\n\n.mplayer_settingsItem.selected {\n  border-left: 2px solid $themeColor;\n  color: $themeColor;\n  background-color: rgb(0, 0, 0);\n}\n\n.mplayer_settingsItem:hover {\n  box-shadow: 0 0 3px #000;\n  background-color: rgb(0, 0, 0);\n}","/* icon or button display control START */\n\n.mplayer_sizer.paused .mplayer_pauseIcon {\n  display: none;\n}\n\n.mplayer_sizer:not(.paused) .mplayer_playIcon {\n  display: none;\n}\n\n.mplayer_sizer.fullscreen .mplayer_fullscreenIcon {\n  display: none;\n}\n\n.mplayer_sizer:not(.fullscreen) .mplayer_exitFullscreenIcon {\n  display: none;\n}\n\n.mplayer_sizer.miniPlayer .mplayer_miniPlayerIcon {\n  display: none;\n}\n\n.mplayer_sizer:not(.miniPlayer) .mplayer_exitMiniPlayerIcon {\n  display: none;\n}\n\n.mplayer_sizer.theater .mplayer_theaterModeIcon {\n  display: none;\n}\n\n.mplayer_sizer:not(.theater) .mplayer_exitTheaterModeIcon {\n  display: none;\n}\n\n.mplayer_sizer:not(.next) .mplayer_playNextButton {\n  display: none;\n}\n\n.mplayer_sizer:not(.previous) .mplayer_playPreviousButton {\n  display: none;\n}\n\n.mplayer_sizer[data-mplayer-volume-level=\"high\"] .mplayer_volumeUpIcon {\n  display: block;\n}\n\n.mplayer_sizer[data-mplayer-volume-level=\"low\"] .mplayer_volumeDownIcon {\n  display: block;\n}\n\n.mplayer_sizer[data-mplayer-volume-level=\"muted\"] .mplayer_mutedIcon {\n  display: block;\n}\n\n.mplayer_sizer.subtitles .mplayer_subtitlesButton {\n  border-bottom: 3px solid rgba(0, 0, 0, 0);\n  box-shadow: 0 3px 0 $themeColor;\n}\n\n/* icon or button display control END */",".mplayer_contextMenu {\n  position: absolute;\n  background-color: rgba(0, 0, 0, .5);\n  border-radius: 3px;\n  flex-direction: column;\n  padding: .25rem 0;\n  z-index: 1;\n  backdrop-filter: blur(var(--blur));\n}\n\n.mplayer_contextMenuItem {\n  padding: .25rem 2rem;\n  font-weight: 800;\n  cursor: pointer;\n}\n\n.mplayer_contextMenuItem:hover {\n  background-color: black;\n  box-shadow: 0 0 3px black;\n}",".mplayer_sizer {\n  position: relative;\n  width: 90%;\n  max-width: 1000px;\n  display: flex;\n  justify-content: center;\n  margin-inline: auto;\n  background-color: black;\n  font-family: sans-serif;\n  --blur: .15vw;\n  height: 100%;\n}\n\n.mplayer_sizer.hideControls {\n  cursor: none;\n  .mplayer_controlsContainer {\n    display: none;\n  }\n}\n\n.mplayer_video {\n  width: 100%;\n}","/* widget styles START */\n\n.mplayer_switch {\n  position: relative;\n  background-color: white;\n  width: 30px;\n  height: 10px;\n  border-radius: 5px;\n  display: inline-block;\n}\n\n.mplayer_switchHandle {\n  position: absolute;\n  top: -25%;\n  width: 15px;\n  height: 15px;\n  background-color: $themeColor;\n  box-shadow: 0 0 3px $themeColor;\n  cursor: pointer;\n  border-radius: 50%;\n  transition: left 50ms ease-in-out, right 50ms ease-in-out, background-color 50ms ease-in-out;\n}\n\n.mplayer_switch[data-mplayer-switch=\"on\"] {\n  background-color: $themeColor;\n}\n\n.mplayer_switch[data-mplayer-switch=\"on\"] .mplayer_switchHandle {\n  right: 0;\n  box-shadow: 0 0 3px rgba(0, 0, 0, 0.9);\n}\n\n.mplayer_switch[data-mplayer-switch=\"off\"] .mplayer_switchHandle {\n  left: 0;\n}\n\n.mplayer_viewMore, .mplayer_checkBox {\n  display: inline-block;\n  height: 100%;\n  align-content: center;\n}\n\n.mplayer_checkBox_checkedIcon, .mplayer_checkBox_uncheckedIcon {\n  display: none;\n}\n\n.mplayer_checkBox[data-mplayer-checkbox=\"checked\"] .mplayer_checkBox_checkedIcon {\n  display: block;\n}\n\n.mplayer_checkBox[data-mplayer-checkbox=\"unchecked\"] .mplayer_checkBox_uncheckedIcon {\n  display: block;\n}\n\n[data-mplayer-playback-rates-group]{\n  text-align: left;\n}\n\n/* widget styles END */",".mplayer_hintsLayer {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  pointer-events: none;\n  z-index: 2;\n  visibility: hidden;\n}",".mplayer_noticeList {\n  position: absolute;\n  left: 20px;\n  bottom: 60px;\n  pointer-events: none;\n}\n\n.mplayer_notice {\n  max-width: 300px;\n  border-radius: 3px;\n  background-color: rgba(0, 0, 0, .5);\n  backdrop-filter: blur(.5vw);\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-content: center;\n  color: white;\n  padding: 5px 10px;\n  margin: 10px 0;\n  box-shadow: 0 0 2px 2px rgba(0, 0, 0, .5);\n  transition: opacity 250ms ease-in-out;\n  white-space: nowrap;\n  overflow: auto;\n}\n\n.mplayer_notice.enter {\n  animation: enter 250ms;\n}\n\n.mplayer_notice.leave {\n  animation: leave 250ms;\n}\n\n@keyframes enter {\n  0% {\n    transform: translateY(50px);\n    opacity: 0;\n  }\n\n  100% {\n    transform: translateY(0);\n    opacity: 1;\n  }\n}\n\n\n@keyframes leave {\n  0% {\n    transform: translateY(0);\n    opacity: 1;\n  }\n\n  100% {\n    transform: translateY(50px);\n    opacity: 0;\n    display: none;\n  }\n}",".mplayer_sizer.mobile {\n  .mplayer_previewImage, .mplayer_playPreviousButton, .mplayer_playNextButton, .mplayer_screenshotButton, .mplayer_miniPlayerButton, .mplayer_theaterModeButton, .mplayer_settings, .mplayer_subtitles {\n    display: none;\n  }\n\n\n\n}",".mplayer_spotlight {\n  display: none;\n}\n\n.mplayer_sizer.spotlight {\n\n  box-shadow: 0 0 32px 0 rgba(1, 1, 1, .5);\n\n  .mplayer_spotlight {\n    display: block;\n    position: absolute;\n    width: 150%;\n    height: 150%;\n    z-index: -1;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    pointer-events: none;\n\n    canvas {\n      position: absolute;\n      width: 100%;\n      height: 100%;\n    }\n  }\n}",".mplayer_sizer.safari {\n  .mplayer_volume {\n    width: 40px;\n  }\n\n  .mplayer_volume:hover {\n    width: auto;\n  }\n\n  .mplayer_spotlight {\n    z-index: 0;\n  }\n\n  .mplayer_video {\n    z-index: 1;\n  }\n}\n\n.mplayer_sizer.firefox {\n  .mplayer_miniPlayerButton {\n    display: none;\n  }\n}"],"sourceRoot":""}]);
